<template>
    <div class="personal">
        <div class="headerp" :class="{'chang':headerScroll}">
            <van-icon class="ioc" :class="{cs:!headerScroll,changio:headerScroll}" name="wap-nav" />
            <div class="search">
                <span class="app-name">陈一天</span>
                <span class="sea">二手无限，尽情搜索</span>
            </div>
            <van-icon class="ioc" :class="{cs:!headerScroll,changio:headerScroll}" name="contact" />
        </div>
        <van-pull-refresh
        v-model="isLoading"
        success-text="刷新成功"
        @refresh="onRefresh"
        >
        <div class="goods">
            <div class="card"  v-for="(i,index) in goodsdata">
                <img :src="JSON.parse(i.images)[0]"/>
                <p>{{i.name}}</p>
                <p>{{i.price}}</p>
            </div>
        </div>
        </van-pull-refresh>
    </div>
  </template>
  
  <script>
  import { Toast } from 'vant';
    export default {
        data(){
            return{
                headerScroll:false,
                isLoading: false,
                goodsdata:[],
                newGoodses:[{img:'../assets/logo.png',goodsName:'ct',Price:'120'},
                {img:'../assets/logo.png',goodsName:'ct',Price:'120'},
                {img:'../assets/logo.png',goodsName:'ct',Price:'120'},
                {img:'../assets/logo.png',goodsName:'ct',Price:'120'}]
            }
        },
        methods:{
            onRefresh() {
                setTimeout(() => {
                    this.isLoading = false;
                    Toast('刷新成功');
                }, 1000);
            },
            pageScroll() {
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
                scrollTop > 100 ? this.headerScroll = true : this.headerScroll = false
            },
        },
        mounted(){
            window.addEventListener('scroll', this.pageScroll)
            this.$api.getgoods({page:this.page}).then(res=>{
            this.goodsdata.push(...(res.data.data));
            this.lenpage= Math.ceil(res.data.total/6);
            })
        }
    }
  </script>
  
  <style lang="less" scoped>
    .iocchang{
        font-size: 24px;
        line-height: 40px;
        margin-left: 20px;
        color: #fff;
    }
    .chang{
        background: #1baeae;
    }
    .goods{
        display: flex;
        flex-wrap: wrap;
    }
    .card{
      --n:2;
      // --gap: calc((100% - 200px * var(--n)) / var(--n) /2);
      --gap: calc((100% - 184px * var(--n)) / var(--n) /2);
      width: 184px;
      height: 255px;
      background: rgb(255, 255, 255);
      margin: 2px var(--gap);
      // margin: 10px; 
      img{
        width: 100%;height: 210px;object-fit: cover;
        }
    }
    .ioc{
        font-size: 24px;
        line-height: 40px;
        margin-left: 20px;
        &.cs{
            color: #1baeae;
        }
        &.changio{
            color: #fff;
        }
    }
    .headerp{
        z-index: 10;
        position: sticky;
        top: 0;
        height: 60px;
        display: flex;
        align-items: center;
        .search{
            margin-left: 25px;
            width: 250px;
            height: 40px;
            background: rgba(255, 255, 255, .7);
            border-radius: 20px;
            .app-name{
                position: relative;
                top: 5px;
                padding-left: 10px;
                color: #1baeae;
                font-size: 20px;
                font-weight: bold;
            }
            .sea{
                position: relative;
                top: 3px;
                padding-left: 40px;
                font-size: 12px;
                color: #666;
            }
        }
    }
  </style>
  